import { useEffect, useState } from 'react';
import { setup } from '../lib/sdk';

export function useDiscordSDK(oauth2_client_id) {
    const [ value, setValue ] = useState({});
    useEffect(() => {
        async function doSetup() {
            const value = await setup(oauth2_client_id);
            setValue(value);
        }
        doSetup();
    }, [oauth2_client_id])

    return value;
}