import { DiscordSDK } from '@discord-external/activity-iframe-sdk';

export async function setup(oauth2_client_id) {
    const discordSdk = new DiscordSDK(oauth2_client_id);

    // Wait for READY payload from the discord client
    await discordSdk.ready();

    // Pop open the OAuth permission modal and request for access to scopes listed in scope array below
    const {code} = await discordSdk.commands.authorize({
        client_id: oauth2_client_id,
        response_type: 'code',
        state: '',
        prompt: 'none',
        scope: ['identify', 'rpc.activities.write', 'applications.channels'],
    });

    // Retrieve an access_token from your activity's server
    const response = await fetch('/api/token', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            code,
        }),
    });
    const {access_token} = await response.json();

    // Authenticate with Discord client (using the access_token)
    await discordSdk.commands.authenticate({
        access_token,
    });

    // Now that you are authenticated, set this activity for the current activity user
    // NOTE: You must call setActivity within the first two minutes of the application's
    // launch or else the iframe will close
    await discordSdk.commands.setActivity({
        activity: {
        type: 0,
        details: 'Homecord',
        state: 'Playing',
        party: {id: discordSdk.clientId},
        assets: {
            large_image: 'servericon',
            small_image: 'servericon-small',
        },
        },
    });

    return { discordSdk, access_token };
}